 const insite = {
     count : 0
 }
 
 const Reducer = (state = insite, action) =>{
    if(action.type == 'INCREASE'){
        return {count : state.count + 1};
    }else if(action.type == 'DECRASER'){
        return {count : state.count - 1};
    }
    return state;
}

export default Reducer;